* {
    margin: 0;
}

html, body {
    height: 100%;
}

a {
    text-decoration:none;
    color: inherit;
}

#root {
    height:100%;
}

.Screen {
    margin: 0 auto;
    max-width: 560px;
    height: 900px;
    border: 1px solid black;
    background-color: #DAFFF2;
    display:grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
}

.App {
    text-align: center;
    background-color: #333333;
    height:100%;
    padding-top:20px;
    font-family: 'Montserrat', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif;
}
.FaultReport {
    height:100%;
    display: grid;
    grid-template-rows:1fr 1fr 5fr;
    grid-template-columns: 1fr;
}

.FaultReport img {
    width:50%;
    margin: auto;
}

.FaultReportTitle {
    color: #682C91;
}

.FaultReportButton {
    width: 30%;
    height: 50px;
    margin:10px;
}

.FaultReportText textarea {
    font-size:15px;
}

.FaultReportHeader {
    font-size: 30px;
}

.FaultReportQuestionsChoices {
    height:30%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5px;
    margin: 5px;
}

.FaultReportQuestionsItem {
    background-color: #682C91;
    padding: auto;
    border-radius: 25px;
}

.FaultReportQuestionsItem div:hover {
    color: #AAAAAA;
}

.FaultReportQuestionsItem:active {
    color: #aaaaaa;
    -webkit-animation: fadeinout 4s linear forwards;
    animation: fadeinout 4s linear forwards;
}

.FaultReportQuestionsQuestion {
    font-size:30px;
    padding: 30px;
}
.FaultReportQuestionsItemText {
    font-size:40px;
    margin-top: 30%;
}


@-webkit-keyframes fadeinout {
  100% { opacity: 1; }
  50% { opacity: 0.5; }
  0% { opacity: 0; }
}

@keyframes fadeinout {
  100% { opacity: 1; }
  50% { opacity: 0.5; }
  0% { opacity: 0; }
}
